let initialState = {
    style: '–ë—Ä—é—Ç'
}

const settingsReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CHANGE_STYLE':
            return {
                ...state,
                style: action.style
            }
        default:
            return state
    }
}

export default settingsReducer